import React from 'react'

export const ToDo = ({ todo, deleteTodoHandler, index, checkedHandler, editTodoHandler }) => {
    const iconStyle = {
        cursor: 'pointer',
        color: 'red'
    }
    const todoStyle = {
        cursor: 'pointer',
        userSelect: 'none'
    }
    const changeTodo = (todo) => {
        const newTodo = window.prompt("Enter new ToDo", todo.name);
        if (newTodo === null || newTodo === "") return;
        editTodoHandler(todo, newTodo);
    }
    const myClassName = (todo.completed) ? "m-0 fs-5 text-decoration-line-through" : "m-0 fs-5";
    return (
        <div className="d-flex align-items-center my-2 px-2">
            <input className="form-check-input mx-2" style={{ cursor: 'pointer' }} type="checkbox" checked={todo.completed} id="flexCheckDefault" onChange={() => checkedHandler(todo)} />
            <p className={myClassName} style={todoStyle} onDoubleClick={() => changeTodo(todo)}>{index + 1}. {todo.name}</p>
            <i className="bi bi-trash d-block ms-auto pe-2 fs-4 text-secondary" onClick={() => deleteTodoHandler(todo)} onMouseEnter={e => { e.target.classList.add("text-danger"); e.target.style = "cursor:pointer; transition:0.25s"; }} onMouseLeave={e => { e.target.classList.remove("text-danger"); e.target.style = "cursor:auto"; }} style={iconStyle}></i>
        </div>
    )
}
