import React from 'react'
import { ToDo } from './ToDo';
export const ToDoList = ({ todos, deleteTodoHandler, checkedHandler, editTodoHandler }) => {
    const completedTodos = todos.filter((e) => { return !e.completed; });
    return (
        <div className="container">
            <h2 className="text-decoration-underline">My ToDos:</h2>
            {todos.map((todo, index) => {
                return <ToDo todo={todo} key={index} index={index} deleteTodoHandler={deleteTodoHandler} checkedHandler={checkedHandler} editTodoHandler={editTodoHandler} />;
            })}
            <p className="text-muted">{completedTodos.length} left to do.</p>
        </div>
    )
}
