import React, { useState } from 'react';
import { useEffect } from 'react';
import { Header } from "./Header";
import { AddToDo } from "./AddToDo";
import { ToDoList } from "./ToDoList";
function App() {
  const [todos, setTodos] = useState([]);
  const [date, setDate] = useState(new Date().toISOString().split('T')[0]);
  useEffect(() => {
    if (localStorage.getItem(date) === null) {
      localStorage.setItem(date, JSON.stringify([]));
    }
    const retrievedTodos = JSON.parse(localStorage.getItem(date));
    setTodos(retrievedTodos);
  }, [date]);
  useEffect(() => { localStorage.setItem(date, JSON.stringify(todos)); });
  const dateHandler = (date) => {
    setDate(date);
  }
  const addToDoHandler = (todo) => {
    const myTodo = { name: todo, completed: false };
    setTodos([...todos, myTodo]);
  }
  const deleteTodoHandler = (todo) => {
    if (!todo.completed) {
      if (!window.confirm("Do you want to delete this todo it is still not completed"))
        return;
    }
    setTodos(todos.filter((e) => {
      return e !== todo;
    }));
  }
  const checkedHandler = (todo) => {
    const newTodos = [...todos];
    const newTodo = newTodos.find((e) => { return e === todo });
    newTodo.completed = !newTodo.completed;
    setTodos(newTodos);
  }
  const clearCompletedHandler = () => {
    if (window.confirm('Are you sure?')) {
      setTodos(todos.filter((e) => { return !e.completed }));
    }
  }
  const editTodoHandler = (todo, newName) => {
    const newTodos = [...todos];
    const newTodo = newTodos.find((e) => { return e === todo; });
    newTodo.name = newName;
    setTodos(newTodos);
  }
  return (<>
    <Header />
    <AddToDo addToDoHandler={addToDoHandler} clearCompletedHandler={clearCompletedHandler} dateHandler={dateHandler} />
    <ToDoList todos={todos} deleteTodoHandler={deleteTodoHandler} checkedHandler={checkedHandler} editTodoHandler={editTodoHandler} />
  </>);
}

export default App;
