import React from 'react'
import { useRef } from 'react';
export const AddToDo = ({ addToDoHandler, clearCompletedHandler, dateHandler }) => {
    const todoref = useRef();
    const addTodo = () => {
        const Todo = todoref.current.value;
        if (Todo === "") {
            alert("No todos to add");
            return;
        }
        addToDoHandler(Todo);
        todoref.current.value = "";
        todoref.current.blur();
    }
    return (
        <div className="container">
            <div className="my-3 text-center">
                <input type="date" className="border-primary rounded" defaultValue={new Date().toISOString().split('T')[0]} onChange={(e) => { dateHandler(e.target.value) }} />
            </div>
            <div className="input-group my-3">
                <input type="text" className="form-control" autoFocus placeholder="Add ToDo" aria-label="ToDos"
                    aria-describedby="button-addon2" ref={todoref} onKeyPress={(e) => { if (e.key === 'Enter') addTodo(); }} />
                <button className="btn btn-outline-primary rounded-end" type="button" onClick={() => addTodo()}>Add Todo</button>
            </div>
            <p className="text-muted d-flex justify-content-between align-items-center">Double click on ToDo to edit. <button className=" btn btn-outline-danger" type="button" onClick={() => clearCompletedHandler()}>Clear Completed</button></p>

        </div>
    )
}
